.aftopicjumper {
	position: relative;
	top: 0;
	left: 0;
	margin-top: -16px;
	width: 100%;
	height: 50px;
	border-bottom: 1px solid #e9e9e9;
	background: #FFF;
	z-index: 23;
	-webkit-transition: all .25s ease-in-out;
	-moz-transition: all .25s ease-in-out;
	-ms-transition: all .25s ease-in-out;
	-o-transition: all .25s ease-in-out;
	transition: all .25s ease-in-out;
}

.aftopicjumper a:hover {
	color: #008fd8;
}
.aftopicjumper a:hover:before {
	background-color: #008fd8;
	color: #FFF !important;
}

.aftopicjumper a:before {
	content: '';
	position: absolute;
	top: 0;
	width: 50px;
	height: 50px;
	background: #e9e9e9;
	-webkit-transition: all .25s ease-in-out;
	-moz-transition: all .25s ease-in-out;
	-ms-transition: all .25s ease-in-out;
	-o-transition: all .25s ease-in-out;
	transition: all .25s ease-in-out;
}

.aftopicjumperdesc {
	color: #666;
}

.afprevtopic a,
.afnexttopic a {
	width: 50%;
	height: 50px;
	float: left;
	position: relative;
	font-size: 11px;
	text-transform: uppercase;
	font-weight: 600;
	text-decoration: none;
	line-height: 50px;
	color: #BBB;
	overflow: hidden;
	-webkit-transition: all .25s ease-in-out;
	-moz-transition: all .25s ease-in-out;
	-ms-transition: all .25s ease-in-out;
	-o-transition: all .25s ease-in-out;
	transition: all .25s ease-in-out;
	text-decoration: none;
}
.afprevtopic a {
	border-right: 1px solid #eee;
}
.afprevtopic a:hover,
.afnexttopic a:hover {
	text-decoration: none;
	color: #008fd8 !important;
}

.afprevtopic a span,
.afnexttopic a span {
	color: #666;
}
.afprevtopic a:hover span,
.afnexttopic a:hover span {
	color: #008fd8;
}

.afprevtopic a:before,
.afnexttopic a:before {
	display: inline-block;
	font: normal normal normal 14px/1 FontAwesome;
	font-size: inherit;
	text-rendering: auto;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	
	color: #bbb !important;
	font-size: 40px;
	line-height: 50px;
	text-align: center;
}

.afprevtopic a {
	padding-left: 70px;
}
.afprevtopic a:before {
	left: 0;
	content: "\f104";
}

.afnexttopic a {
	padding-right: 70px;
	text-align: right;
}
.afnexttopic a:before {
	left: auto;
	right: 0;
	content: "\f105";
}

