	height: 30px;
	line-height: 30px;*/
	color: rgba(0, 0, 0, 0.3);
	font-weight: normal;
}


.af-search-duration {
	float: right;
}

.af-search-footer {
	position: relative;
	margin: 10px 0px;
}

.af-search-footer .afpager {
	position: absolute;
	top: 0px;
	right: 0px;
}

.af-search-header .af_pager, 
.af-search-footer .af_pager {
	padding: 0px 5px;
}

.af-search-criteria {
	display: inline-block;
	margin-right: 10px;
	color: rgba(0, 0, 0, 0.3);
}

.af-search-criteria b {
	color: rgba(0, 0, 0, 0.6);
}

.af-search-noresults {
	text-align: center;
	color: rgba(0, 0, 0, 0.44);
	padding: 50px 0px;
	font-size: 20px;
	letter-spacing: -0.02em;
}

.af-search-results a {
	font-weight: bold;
}

/* Search Result - Posts - Added 5.0.4 */
.af-search-results .af-post {
	border: solid 1px #999;
	padding: 0px;
	margin: 4px 0px 0px;
	background-color: #f0f0f0;
}

.af-search-results .af-post-header {
	background-color: #ddd;
	padding: 4px 10px;
	color: #3d3d3d;
	font-weight: bold;
}

.af-search-results .af-post-header .af-stats {
	float: right;
	font-weight: normal;
	white-space: nowrap;
}

.af-search-results .af-post-header label {
	display: inline-block;
	width: 50px;
}

.af-search-results .af-post-header .af-stats span {
	display: inline-block;
	width: 50px;
	text-align: right;
}

.af-search-results .af-post-content {
	padding: 10px;
}

/* Search Results - Topics - Added 5.0.4 */
.af-search-results .af-topics {
	width: 100%;
	border-collapse: collapse;
	margin: 0px;
	padding: 0px;
	/* background-color: #f0f0f0; */
}

.af-search-results .af-topics tr {
	border: solid 1px #999;
	border-top: none;
}

.af-search-results .af-topics td {
	padding: 10px;
	vertical-align: top;
	text-align: left;
}

.af-search-results .af-topics .af-icon {
	vertical-align: middle;
	/* background-color: #ddd; */
	text-align: center;
	width: 32px;
}

.af-search-results .af-topics .af-icon img {
	border: none;
}

.af-search-results .af-topics .af-thread {
}

.af-search-results .af-topics .af-stats {
	text-align: right;
	/* background-color: #ddd; */
	white-space: nowrap;
	width: 70px;
}

.af-search-results .af-topics .af-lastpost {
	width: 200px;
}

.af-search-results .af-topics .af-forum {
	background-color: #ddd;
	width: 150px;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

.af-search-results .af-topics .af-mini-pager {
	display: inline-block;
	margin-left: 10px;
	white-space: nowrap;
}

.af-search-results .af-topics .af-mini-pager a {
	display: inline-block;
	border: solid 1px #999;
	text-decoration: none;
	font-weight: normal;
	font-size: 0.8em;
	height: 16px;
	min-width: 12px;
	text-align: center;
	line-height: 16px;
	padding: 0px 2px;
	margin: 0px 1px;
}

.af-search-results .af-topics .af-mini-pager span {
	padding: 0px 2px;
}

/* Profile Edit - Added 5.0.4 */
.af-button-edituser {
}

.af-button-edituser .ui-icon {
	vertical-align: text-top;
}

.af-button-edituser .ui-button-text {
	padding: 0px 5px;
}

/* Additions for new theme (Timo Breumelhof 20190621) */

.afcontainer {
    font-family: inherit;
    font-size: 1em
}

.activeForums {
