}

.activeForums .afdropdown,
.activeForums .afcontainer,
.activeForums .aftextbox,
.activeForums .afgrid,
.activeForums .afnormal,
.activeForums blockquote {
    font-family: inherit;
    font-size: 1em
}


.activeForums ul.dnnAdminTabNav {
    height: 40px
}

.activeForums ul.dnnAdminTabNav li.ui-tabs-active a {
    background-color: #bf4044;
    text-shadow: none;
    padding: 10px 14px 9px
}

.activeForums h2.dnnFormSectionHead {
    margin-top: 1em;
    margin-bottom: .5em;
    border-bottom: 2px solid #bf4044;
