}

.activeForums .dnnLogHeader {
    background: #bf4044;
    color: #fff
}

.activeForums .dnnFormMessage {
    border-radius: 0;
    border: none;
    max-width: 100%
}

.activeForums .dnnForm .ui-corner-all {
