}

.activeForums ul.dnnAdminTabNav {
    border-bottom: solid 2px #bf4044;
    margin-top: .5em
}

.activeForums ul.dnnAdminTabNav li.ui-tabs-active a {
    margin-top: 0
}

.activeForums .LabelEditTextClass,
.activeForums .fffas-select {
    color: #501b1d
}

.activeForums .tile-af {
    border-bottom: solid 2px #b3d7e6;
    padding: 1em
}

.activeForums .tile-af .ct-forumname {
    font-weight: bold;
    font-size: 2em;
    margin-bottom: .5em;
    display: block
}

.activeForums .Connect-SearchBarInput * {
    vertical-align: middle
}

.activeForums .Connect-SearchBarInput input {
    margin-right: 1em;
    width: 250px;
    padding: .36em .5em;
    font-size: 16px;
    color: #409bbf;
    border: solid 2px #136586;
    box-sizing: content-box
}

.activeForums .Connect-SearchBarInput a {
    margin: 0
}

.activeForums .fa-prev-next {
    margin-bottom: 1em;
    margin-top: .7em;
    padding-bottom: .7em;
    border-bottom: solid 2px #dcdcdc
}

.activeForums .fa-prev-next img {
    display: none
}

.activeForums .fa-prev-next a {
    color: #409bbf
}

.activeForums .fa-prev-next a:before {
    font-family: FontAwesome;
    font-size: 1.5em;
    line-height: 1.5em;
    background: #409bbf;
    display: inline-block;
    width: 1.5em;
    text-align: center;
    color: #fff
}

.activeForums .fa-prev-next a:after {
    font-family: FontAwesome;
    font-size: 1.5em;
    line-height: 1.5em;
    background: #409bbf;
    display: inline-block;
    width: 1.5em;
    text-align: center;
    color: #fff
}

.activeForums .fa-prev a:before {
    content: "\f104 ";
    margin-right: .5em
}

.activeForums .fa-next a:after {
    content: "\f105 ";
    margin-left: .5em
}

.activeForums .af-topic header {
    padding: 0;
    margin-bottom: .5em;
    border-bottom: solid 2px #dcdcdc
}

.activeForums .af-topic header h2 {
    margin: 0 0 .3em;
    font-size: 1.5em
}

.activeForums .af-topic .dnnPrimaryAction {
    margin-bottom: 0
}

.activeForums .af-crumb {
    line-height: 2.5em
}

.activeForums .af-topic-controls {
    clear: both;
    padding: .7em 0
}

.activeForums .af-topic-controls .af-sort select {
    margin-left: .5em
}

.activeForums .af-topic-head {
    line-height: 40px;
    color: #000;
    font-weight: bold
}

.activeForums .af-topic-item {
    clear: both;
    margin: 0 0 2em
}


.activeForums .af-topic-item .af-topic>div {
    padding-top: 1em;
    padding-bottom: 1em;
    background: #f5f8f9
}

.activeForums .af-topic-item .af-topic>div:nth-child(1) {
    background: #e5edf0
}

.activeForums .af-topic-item .af-topic .af-topic-content-top,
.activeForums .af-topic-item .af-topic .body {
    border-bottom: solid 2px #e5edf0
}

.activeForums .af-topic>div {
    background-color: transparent
}

.activeForums .af-topic>div .body {
    padding-top: 1em;
    padding-bottom: 2em
}

.activeForums .af-topic>div .signature {
    padding-top: .8em;
    padding-bottom: 1em;
    max-height: 10em;
    overflow: hidden
}

.activeForums .af-topic>div .signature * {
    font-size: 1em !important
}

.activeForums .af-topic-info>span {
    display: block
}

.activeForums .af-topic-info>span.af-user-avatar {
    padding: 1em 0
}

.activeForums .af-topic-info br:first-child {
    display: none
}

.activeForums .af-topic-info .af-avatar {
    max-width: 100%;
    height: auto !important;
    padding-top: .5em
}

.activeForums .af-user-edit {
    padding-top: .3em;
    text-align: right;
    margin-right: 1em
}

.activeForums .af-user-name span {
    display: inline-block;
    vertical-align: middle;
    line-height: 1.5em;
