}

.activeForums .af-topic-content-top {
    padding-bottom: .7em;
    margin-bottom: .7em
}

.activeForums .af-topic-actions ul {
    list-style-type: none;
    padding: 0;
    margin: 0
}

.activeForums .af-topic-actions li {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: inline-block;
    margin-right: .5em;
    cursor: pointer
}

@media only screen and (max-width:767px) {
    .activeForums .af-topic-info .af-user-avatar {
        position: absolute;
        right: 10px;
        top: 0
    }
    .activeForums .af-topic-content-top {
        padding: .7em;
        margin: .7em 0;
        border-top: solid 2px #bfd1d9
    }
    .activeForums .af-topic-content-top .af-topic-actions {
        padding-top: .5em
    }
}

@media only screen and (min-width:768px) {
    .activeForums .wrap-full-height {
        overflow: hidden
    }
    .activeForums .full-height {
        margin-bottom: -99999px;
        padding-bottom: 99999px
    }
    .activeForums .af-topic-content-top .af-topic-actions {
        text-align: right
    }
    .activeForums .af-topic-actions li {
        margin-left: .5em;
        margin-right: 0
    }
}

.activeForums .af-quickreply {
    padding: 2em 0;
    margin-top: 1em;
    border-top: solid 2px #b3d7e6
}

.activeForums .af-quickreply table td {
    padding-right: .5em
}

.activeForums .af-quickreply .amtbwrapper {
    padding-top: 1em
}


